.post {
    display: flex;
    width: 800px;
    height: 250px;
    color: rgba(255,255,255,0.3);
    border-bottom: 1px solid rgba(255,255,255,0.3);
    margin: 20px;
    transition: 0.5s;
}
.post-details {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 200px;
    padding: 8px;
    padding-top: 0;
}

.post-img {
    width: 300px;
    height: 200px;
}

.post-details .heading{
    color: yellowgreen;
    font-size: 24px;
    text-transform: uppercase;
    font-weight: bold;
}

.post-details .short-desc{
    font-size: 16px;
}

.post-details .post-by{
    display: flex;
    font-size: 12px;
}
.post-details .post-by .author{
    margin-right: 20px;
}

@media (max-width: 992px) {
    .post {
        flex-direction: row-reverse;
        width: 300px;
        height: 280px;
    }
    .post-details{
        padding: 0px;
        height: 250px;
    }
    .post-img {
        width: 100px;
        height: 80px;
    }
    ..post-details .heading {
        font-size: 18px;
    }
    .post-details .short-desc {
        font-size: 12px;
    }
    .post-details .post-by .author{
        font-size: 8px;
    }
    .post-details .post-by .time-ago{
        font-size: 8px;
    }
}

