.post-page {
    height: 100%;
    color: white;
    display: flex;
    padding-left: 200px;
    padding-right: 400px;
    flex-direction: column;
    align-items: flex-start;
}
.post-page .short-desc {
    font-size: 18px;
    margin-top: 15px;
    margin-bottom: 15px;
}
.post-page .heading {
    font-size: 36px;
    font-weight: bold;
    /* text-align: center; */
    margin-top: 50px;
}
.post-page .post-by {
    font-size: 11px;
    margin-bottom: 10px;
}

.post-by .author {
    text-transform: uppercase;
    font-weight: bold;
}

.post-page .post-img {
    width: 750px;
    height: 400px;
    align-self: center;
}

.post-page .post-content {
    margin-top: 100px;
    font-size: 18px;
}
