.navbar {
    height: 50px;
    width: 100%;
    background-color: #808080;
    position: sticky;
    top: 0;
    left: 0;
}
.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.9);
    overflow-x: hidden;
    transition: 0.5s;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
}
.nav-link-container {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 100px;
}
.nav-links {
    margin: 8px;
    text-decoration: none;
    font-size: 18px;
    color: #818181;
    display: inline;
    text-transform: uppercase;
    transition: 0.3s;
}
.sidenav a:hover {
    color: #f1f1f1;
}

.sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
    color: white;
    cursor:pointer;
}

.nav-footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 50px;
}

.follow-us{
    display: flex;
    flex-direction: column;

}

.follow-us p{
    margin: 0;
}

.follow-us .social-icons{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.follow-us .social-icons a{
    color: inherit;
}

@media screen and (max-height: 450px) {
    .nav-link-container {
        padding: 50px;
    }
}
