.all-post-container{
    display: flex;
    height: fit-content;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media (max-width: 992px) {
    .all-post-container{
        flex-direction: row;
        flex-wrap: wrap;
        padding: 0;
        justify-content: space-around;
        align-items: flex-start;
    }
 }